import fontRefRegular from "./assets/Alata-Regular.ttf";
import defaultSmall from "./assets/defaultsmall.jpeg";
import defaultBig from "./assets/defaultbig.jpeg";

export const handler = async ({ inputs, mechanic, sketch }) => {
  const { width, height, area, disenador, tipologia, nombre, pais, ano, atributos, imagen1, imagen2} = inputs;
  const center = [width / 2, height / 2];
  let img1;
  let imgGraphic1;
  let img2;
  let imgGraphic2;
  let fontRegular;

  const areaCode = {
    "Forma": "#8fe2e2",
    "Función": "#fc9677",
    "Técnica": "#fcc77a"
  }

  // const font = await preloadFont('https://fonts.gstatic.com/s/alata/v9/PbytFmztEwbIoceyzqYzQ1OW7l8.woff2');
  const loadImageAndAddFilter = (img) => {
    const imgGraphic = sketch.createGraphics(img.width, img.height);
    imgGraphic.image(img, 0, 0);
    return imgGraphic
  };

  sketch.preload = async () => {
    if (imagen1) {
      img1 = sketch.loadImage(URL.createObjectURL(imagen1));
    } else {
      img1 = sketch.loadImage(defaultBig);
    }
    if (imagen2) {
      img2 = sketch.loadImage(URL.createObjectURL(imagen2));
    } else {
      img2 = sketch.loadImage(defaultSmall);
    }
    fontRegular = sketch.loadFont(fontRefRegular);
  }

  sketch.setup = () => {
    sketch.createCanvas(width, height);
    if (img1) {
      imgGraphic1 = loadImageAndAddFilter(img1);
    }
    if (img2) {
      imgGraphic2 = loadImageAndAddFilter(img2);
    }
    console.log(fontRegular);
    sketch.textFont(fontRegular);
  };

  sketch.draw = () => {
    sketch.background(255);
    sketch.noStroke();

    const ux = width/10;
    const uy = height/19;

    sketch.fill(areaCode[area]);
    sketch.rect(0,0,ux,height);
    sketch.rect(width-ux,0,ux,height);

    sketch.noStroke();
    sketch.fill("#6b6b6b");

    // COLUMNA IZQUIERDA
    sketch.textAlign(sketch.LEFT, sketch.TOP);

    // Diseñador
    sketch.textSize(width * 0.04);
    sketch.text(disenador.substring(0,27),ux*2,uy*3,ux*6);

    // Tipología
    sketch.textSize(width * 0.06);
    sketch.text(tipologia,ux*2,uy*4);

    // Imagen1
    sketch.fill(100);
    if (imgGraphic1) {
      sketch.image(imgGraphic1,ux*2,uy*6.4,ux*6,ux*6,0,0,imgGraphic1.width,imgGraphic1.height,sketch.COVER);
    } else {
      sketch.rect(ux*2,uy*6.4,ux*6,ux*6);
    }

    // Nombre
    sketch.textLeading(ux*0.7);
    sketch.textSize(width * 0.07);
    sketch.text(nombre,ux*2,(height/20)*5,ux*5);

    // COLUMNA DERECHA
    sketch.textAlign(sketch.RIGHT, sketch.TOP);
    
    // País
    sketch.textSize(width * 0.04);
    sketch.text(pais,ux*8,uy*4.2);

    // Año
    sketch.textSize(width * 0.04);
    sketch.text(ano.substring(0,4),ux*8,(height/20)*5);

    // COLUMNA IZQUIERDA
    sketch.textAlign(sketch.LEFT, sketch.TOP);

    // Atributos
    sketch.textLeading(ux*0.6);
    sketch.textSize(width * 0.04);
    sketch.text(atributos,ux*2,uy*13.6,ux*6);

    sketch.textLeading();
    
    // console.log(imgGraphic1);
    
    if (imgGraphic2) {
      sketch.image(imgGraphic2,ux*5,uy*15,ux*3,ux*3,0,0,imgGraphic2.width,imgGraphic2.height,sketch.COVER);
    } else {
      sketch.rect(ux*5,uy*15,ux*3,ux*3);
    }

    sketch.push();

    sketch.translate(...center);

    // if (imageFile) {
    //   sketch.image(img,0,0,100,100);
    // }

    sketch.pop();

    // const img = new Image();
    // img.onload = function () {
    //   ctx.drawImage(img, 0, 0, height, height);
    //   mechanic.done(canvas);
    // };
    // img.src = image ? URL.createObjectURL(image) : "";

    mechanic.done();
  };
};

